.main-article {
  $height-banner: 100vh;
  position: relative;
  display: flex;
  align-items: center;
  height: $height-banner;
  background-position: center;
  background-repeat: no-repeat !important;
  background-size: cover !important;

  &::after {
    content: '';
    display: block;
    width: 100%;
    height: $height-banner;
    background: rgba(#000, 0.8);
    position: absolute;
    top: 0;
    left: 0;
  }
  
  @media only screen and (max-width: 600px) {
    $height-banner: 450px;

    height: $height-banner;

    &::after {
      height: $height-banner;
    }
  }

  &__contents {
    position: relative;
    z-index: 2;
  }

  @mixin metadatas {
    padding: 3px 36px;
    border: 1px solid var(--color-content-border);
    border-radius: 3px;
  }

  &__category {
    @include metadatas;
    background-color: var(--color-alt-content-bg);
    color: var(--color-alt-content-text);
    margin-right: 8px;
  }
  
  &__time {
    @include metadatas;
    color: var(--color-content-text);
  }

  &__title {
    color: var(--color-content-text);
    max-width: 812px;
  }

  &__button {
    background-color: var(--color-content-primary);
    border-radius: 3px;
    padding: 19px 78px;
    border: none;
    color: var(--color-content-text);

    .for-mobile {
      display: none;
    }
    
    @media screen and (max-width: 600px) {
      span {
        display: none;
      }

      .for-mobile {
        display: inline-block;
      }
    }
    cursor: pointer;
  }
}

.list-categorys-posts {
  overflow: hidden;

  .main {
    $space-elements: 12px;

    &__category-title {
      color: var(--color-alt-content-title);
      margin-bottom: 32px;
      font-size: clamp(24px, 3vw, 36px);
    }
    
    &__post-item {
      cursor: grab;
    }
    
    &__attached-image {
      margin-bottom: $space-elements;
      height: 260px;

      img {
        object-fit: cover;
        height: 100%;
      }
    }
    
    &__time {
      margin-bottom: $space-elements;
      padding: 3px 36px;
      border: 1px solid var(--color-content-border);
      border-radius: 3px;
      color: var(--color-content-text);
      display: inline-block;

      @media screen and (max-width: 600px) {
        font-size: 0.75rem;
        line-height: 1;
      }
    }

    &__title {
      color: var(--color-content-title);
      font-weight: 500;
    }
  }

}
